.skills {
  padding: 4rem;
  background-color: var(--background-color);
  color: var(--text-color);
}

.skills h2 {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  text-align: center;
  animation: fadeIn var(--transition-speed) ease-out;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
  animation: fadeInUp var(--transition-speed) ease-out;
}

.skill-item {
  background-color: #2c3e50;
  border-radius: 10px;
  padding: 1rem;
  width: 200px;
  text-align: center;
}

.skill-item h3 {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.chart-container {
  width: 150px;
  height: 150px;
  margin: 0 auto;
}
.soft-skills {
  margin-top: 3rem;
  text-align: center;
  animation: fadeIn var(--transition-speed) ease-out;
}

.soft-skills h3 {
  font-size: 2rem;
  margin-bottom: 1.5rem;
}

.soft-skills ul {
  list-style: none;
  padding: 0;
}

.soft-skills li {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
